.example {
  padding-top: 25px;
  padding-bottom: 40px;
  background-color: #eaeaea;
}

.example__title {
  margin: 0;
  margin-bottom: 40px;
}

.example__text {
  margin: 0;
  margin-bottom: 20px;
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}

.example__description {
  margin: 0;
  margin-bottom: 20px;
}

.example__wrap {
  display: flex;
  justify-content: space-between;
  gap: 32px;
  margin-bottom: 34px;
}

.example__inner {
  width: 124px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
}

.example__wrapper {
  position: relative;
}

.example__range-bar {
  position: absolute;
  width: 4px;
  height: 256px;
  border-radius: 2px;
  background-color: #666666;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  &:hover {
    background-color: #68b738;
  }

  &:active {
    background-color: #5eaa2f;
  }
}

.example__range-toggle {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 50%;
  right: 50%;
  transform: translateY(-50%) translateX(50%);
  border-radius: 50%;
  border: 2px solid #666666;
  background-color: #ffffff;

  &::before {
    position: absolute;
    content: "";
    border: 10px solid transparent;
    border-left: 10px solid #666666;
    left: 22px;
    top: 50%;
    transform: translateY(-50%);
  }

  &::after {
    position: absolute;
    content: "";
    border: 10px solid transparent;
    border-right: 10px solid #666666;
    right: 22px;
    top: 50%;
    transform: translateY(-50%);
  }

  &:hover {
    border-color: #68b738;

    &::after {
      border-right-color: #68b738;
    }

    &::before {
      border-left-color: #68b738;
    }
  }

  &:active {
    border-color: #5eaa2f;

    &::after {
      border-right-color: #5eaa2f;
    }

    &::before {
      border-left-color: #5eaa2f;
    }
  }
}

@media (min-width: @tablet-width) {
  .example__title {
    margin-bottom: 60px;
    grid-column: 1/-1;
  }

  .example {
    padding-top: 45px;
    padding-bottom: 101px;
    background-image: linear-gradient(to bottom, #ffffff, #ffffff 57%, #eaeaea 57%, #eaeaea 100%);
  }

  .example__wrapper {
    display: grid;
    grid-template-columns: 367px 1fr;
    align-items: center;
    position: relative;
  }

  .example__description {
    margin-bottom: 55px;
    grid-column: 1/-1;
  }

  .example__wrap {
    width: 367px;
    margin: 0;
  }

  picture {
    grid-column: 1/-1;
  }

  .example__img {
    display: block;
    margin: 0 auto;
    grid-column: 1/-1;
    margin-top: 99px;
  }

  .example__text {
    margin-bottom: 0;
    font-size: 20px;
    line-height: 20px;
    text-align: end;
  }

  .example__price {
    display: block;
    margin-top: 9px;
  }

  .example__inner {
    width: 162px;
  }

  .example__range-bar {
    height: 512px;
  }
}

@media (min-width: @desktop-width) {
  .example {
    padding-top: 0;
    padding-bottom: 75px;
    background-image: linear-gradient(to top, #f2f2f2, #f2f2f2 462px, #ffffff 462px, #ffffff 100%);
  }

  .example__wrapper {
    grid-template-columns: 433px 1fr;
    position: relative;
  }

  .example__title {
    margin-bottom: 146px;
    grid-column: 1/2;
  }

  .example__description {
    margin-bottom: 69px;
    grid-column: 1/2;
  }

  .example__wrap {
    grid-column: 1/2;
    margin-bottom: 55px;
  }

  .example__text {
    grid-column: 1/2;
    text-align: start;
  }

  .example__price {
    display: inline-block;
    margin-top: 0;
    margin-left: 63px;
  }

  .example__img {
    margin-top: 0;
    grid-column: 2/3;
    position: absolute;
    top: 0;
    right: 0;
  }

  .example__range-bar {
    right: 276px;
    left: auto;
    top: 0;
  }
}
